@import url('https://fonts.googleapis.com/css?family=Quicksand');


body{
  font-family: 'Quicksand', sans-serif;
}

h1, h4{
  font-family: 'Quicksand', sans-serif;
  text-align: center;
  color: black;
}

p{
  font-size: 20px;
}

/*NAVIGATION ============*/
ul{
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

li{
  font-size: 25px;
}

a{
  font-family: 'Quicksand', sans-serif;
  text-decoration: none;
  color: black;
}
/*END NAVIGATION ==========*/

#home{
  background-image: -webkit-gradient(linear, right top, left top, from(#0F7BFF), to(#FF0FCB));
  background-image: -webkit-linear-gradient(right, #0F7BFF, #FF0FCB);
  background-image: -o-linear-gradient(right, #0F7BFF, #FF0FCB);
  background-image: linear-gradient(-90deg, #0F7BFF, #FF0FCB);
  height: 100vh;
}

/*HEADER ============*/
#headerName{
  font-family: 'bromello', 'Quicksand', 'sans-serif';
  font-size: 140px;
  color: white;
  padding-top: 1em;
  padding-bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 5px;
}

#headerTitle{
  font-family: 'Quicksand', sans-serif;
  color: white;
  font-size: 35px;
  margin-top: -3em;
  padding-bottom: 2.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
/*END HEADER =============*/

/*=============END HOME==============================*/
/*==================================================*/

/*==============START NAV LINK ROUTING====================*/

/*START ABOUT PAGE =======*/
#aboutHeader{
  height: 14em;
  background-image: -webkit-gradient(linear, right top, left top, from(#F6FF00), to(#FF00A1));
  background-image: -webkit-linear-gradient(right, #F6FF00, #FF00A1);
  background-image: -o-linear-gradient(right, #F6FF00, #FF00A1);
  background-image: linear-gradient(-90deg, #F6FF00, #FF00A1);
}

#profilePic{
  margin: 15px 55px;
  float: left;
}

#profilePicImage{
  border-radius: 125px;
}

#aboutH1 {
  font-family: 'bromello', 'Quicksand', 'sans-serif';
  font-size: 80px;
  color: white;
  padding: 1em 0.3em;
  margin-top: 1px;
  text-align: left;
}

.aboutMeText{
  text-align: left;
  font-size: 20px;
  margin-left: 3em;
  margin-right: 3em;
}

#aboutMe p{
  text-align: left;

  margin-right: 25px;
}

#downloadResume a{
  color: orange;
}
/*END ABOUT PAGE =======*/


/*START PORTFOLIO PAGE ========*/
#portfolioHeader{
  height: 14em;
  background-image: -webkit-gradient(linear, right top, left top, from(#F6FF00), to(#FF00A1));
  background-image: -webkit-linear-gradient(right, #F6FF00, #FF00A1);
  background-image: -o-linear-gradient(right, #F6FF00, #FF00A1);
  background-image: linear-gradient(-90deg, #F6FF00, #FF00A1);
}

#portfolioTop {
  font-family: 'bromello', 'Quicksand', 'sans-serif';
  font-size: 80px;
  color: white;
  padding: 1em 0.5em;
  margin-top: 1px;
  text-align: left;
}

#moreProjects{
  text-align: left;
  margin-left: 2em;
}

#pictureRow{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 15px;
}

#contentHeader{
  margin-left: 2em;
}

#contentText p{
  text-align: left;
  margin-left: 2em;
}

#viewCodeLink{
  text-align: center;
}

.portfolioLinks{
  text-decoration: underline;
}

#mojoSiteContainer{
  margin-left: 5em;
}

#portfolioRow1{
  display: -ms-flexbox;
  display: flex;
}

#portfolioRow2{
  display: -ms-flexbox;
  display: flex;
}

/*PERSONAL BRANDING ======*/
#logoDesigns{
  margin-left: 5em;
  margin-right: 5em;
}

#fonts{
  margin-left: 5em;
  margin-right: 5em;
}

#brandBoardContainer{
  display: -ms-flexbox;
  display: flex;
  margin-left: 5em;
  margin-right: 5em;

}

/*END PERSONAL BRANDING ======*/


/*QUICK SNEAKERS PROJECT ======*/

#quickSneakersCode{
  text-decoration: underline;
}

/*END QUICK SNEAKERS PROJECT ========*/

/*START JAVASCRIPT CALCULATOR ===*/
#calculatorContainer{
  margin: 15px 55px;
  float: left;
}

#theProcess{
  margin-left: 3em;
}

.jsCalcCode{
  margin-left: -4em;
  font-size: 20px;
  text-decoration: underline;
  color: blue;
}

#htmlCodeSnip{
  margin-left: 3em;
}

/*END JAVASCRIPT CALCULATOR ====*/

/*START INTRANET REDESIGN*/

#mojoFinalProduct{
  margin-left: 3em;
}


/*END INTRANET REDESIGN*/




