  }


/*START MEDIA QUERY ========*/
@media(max-width: 900px) {
  #pictureRow {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .showcase p {
    width: 100% !important;
  }

  #portfolioRow1{
    -ms-flex-direction: column;
        flex-direction: column;
  }

